'use client'
import Form from 'react-bootstrap/Form';
import { Table } from "react-bootstrap";

let foo = [
    {
        Col1: "dshjetoghjohgijoghijdtoiphjtdoijtdhidtuhgildtuhgliu",
        Col2: "Value2",
        Col3: "Value3"
    },
    {
        Col1: "Value1",
        Col2: "Value2",
        Col3: "Value3"
    },
    {
        Col1: "Value1",
        Col2: "Value2",
        Col3: "Value3"
    },
    {
        Col1: "Value1",
        Col2: "Value2",
        Col3: "Value3",
        Col4: "Value4",
    },
    {
        Col1: "Value1",
        Col2: "Value2",
        Col3: "Value3"
    }
]

function BasicTable(p) {

    let {records} = p;

    //records = foo;

    let header = Object.keys(records[0]);

    //set the rows

    let tableRows = [];

    for(let r in records){

        let details = Object.values(records[r]) 

        tableRows.push(<tr>{details.map(e=>{return<td>{e}</td>})}</tr>) 
    }

  return (
        <Form.Group>
            <Table className="table table-secondary table-hover table-striped table-bordered">
                <thead>
                    <tr>
                        {header.map(e=>{return <th>{e}</th>})}
                    </tr>
                </thead>
                <tbody>
                    {tableRows.map(e=>{return e})}
                </tbody>
            </Table>
        </Form.Group>
  );
}

export default BasicTable;